<template>
    <div id="common-box">
        <div class="user-box">
            <h4>考试成绩</h4>
            <div class="test-form">
                <el-table
                    height="420"
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column align="center" label="考试名称" prop=""></el-table-column>
                    <el-table-column align="center" label="考试类别" prop=""></el-table-column>
                    <el-table-column align="center" label="" prop=""></el-table-column>
                    <el-table-column align="center" label="" prop=""></el-table-column>     
                    <el-table-column align="center" label="考试成绩" prop=""></el-table-column>
                </el-table>
            </div>

            <!-- 底部导航 -->
            <div class="pagination">
                <Pagination
                :totalMsg="query.totalMsg"
                :pageIndex="query.pageIndex"
                :pageSize="query.pageSize"
                @setPageIndex="getPageIndex"
                @setPageSize="getPageSize"></Pagination>
            </div>
        </div>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'
export default {
  components:{
    Pagination
  },
  data(){
    return{
      query:{
          pageSize:5,
          pageIndex:1,
          totalMsg:null,
      },
      tableData:[]
    }
  },
  methods:{
    getPageSize(val){
        this.query.pageSize=val;
        this.getAllGrade();
    },
    getPageIndex(val){
        this.query.pageIndex=val;
        this.getAllGrade();
    },
    getAllGrade(){
      this.$api.stugrade().then();
    }
  },
  created(){
    this.getAllGrade();
  }
}
</script>

<style lang="scss" scoped>

      .user-box{
          margin: 0 auto;
          height: 540px;
          width: 100%;
          background: #fff;
          border: 1px solid #e5e5e5;
          box-sizing: border-box;
          padding: 20px;
          
      }
      .pagination{
          float: right;
          margin: 10px 20px;
      }
</style>